import {
  TextBoxLarge,
  ItemBox,
  BoxImg,
  Img,
  ItemsName,
  ItemsText,
  TextBox,
  TextBoxLast,
} from './BasketItem.styled';

export const BasketItem = ({
  picture1x,
  picture2x,
  name,
  number,
  price,
  sum,
  piece,
  weight,
  unit,
  id,
}) => {
  return (
    <ItemBox>
      <BoxImg>
        <Img src={picture1x} alt={name} srcSet={`${picture2x} 2x`} />
      </BoxImg>
      <TextBoxLarge>
        <ItemsName>{name}</ItemsName>
        <TextBox>
          <ItemsText>{unit === '' ? ':' : `:`}</ItemsText>
          <ItemsText>{unit === '' ? weight + ` ` : piece}</ItemsText>
        </TextBox>
        <TextBox>
          <ItemsText>
            {unit === '' ? `  :` : `  ${number} :`}
          </ItemsText>
          <ItemsText>{price} </ItemsText>
        </TextBox>
        <TextBoxLast className="last">
          <ItemsText>:</ItemsText>
          <ItemsText>{sum} </ItemsText>
        </TextBoxLast>
      </TextBoxLarge>
    </ItemBox>
  );
};
